import React, { useState, useEffect } from 'react';
import { HiEye } from 'react-icons/hi';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const ProductManagement = ({ shop }) => {
  const [products, setProducts] = useState(() => {
    const initialData = {
      home: [
        {
          id: 1,
          productId: "P001",
          name: "  ",
          adminTag: "",
          displayName: "  ",
          price: 9900,
          startDate: "2024-03-01",
          endDate: "2024-03-31",
          description: "AVA       .",
          description2: "      .",
          image: "https://dl.gostop.pmang.cloud/upload/Dia_653_1707.png",
          detailImage: "https://dl.gostop.pmang.cloud/upload/Dia_653_1707_detail.png",
          isVisible: true,
          category: ""
        },
        {
          id: 2,
          productId: "P002",
          name: ") (10)",
          adminTag: "",
          displayName: " 10 ",
          price: 9900,
          startDate: "2024-03-01",
          endDate: "2024-03-31",
          description: "  10 ",
          description2: "     .",
          image: "https://dl.poker.pmang.cloud/upload/mp_mob_ruby_11000.png",
          detailImage: "https://dl.poker.pmang.cloud/upload/mp_mob_ruby_11000_detail.png",
          isVisible: true,
          category: ""
        }
      ],
      newmajgo: [
        {
          id: 1,
          productId: "NM001",
          name: "",
          adminTag: "",
          displayName: " ",
          price: 5900,
          startDate: "2024-03-01",
          endDate: "2024-03-31",
          description: " ",
          description2: "   ",
          image: "https://dl.gostop.pmang.cloud/upload/luckyplus_shop.png",
          detailImage: "https://dl.gostop.pmang.cloud/upload/luckyplus_shop_detail.png",
          isVisible: true,
          category: ""
        },
        {
          id: 2,
          productId: "NM002",
          name: "2+ + 14",
          adminTag: "",
          displayName: " ",
          price: 44000,
          startDate: "2024-03-01",
          endDate: "2024-03-31",
          description: " 2 , 14 ",
          description2: "   !",
          image: "https://dl.gostop.pmang.cloud/upload/bigdaebak_20b.png",
          detailImage: "https://dl.gostop.pmang.cloud/upload/bigdaebak_20b_detail.png",
          isVisible: true,
          category: "()"
        }
      ],
      poker: [
        {
          id: 1,
          productId: "PK001",
          name: " (15)",
          adminTag: "",
          displayName: "  15 ",
          price: 11900,
          startDate: "2024-03-01",
          endDate: "2024-03-31",
          description: "   15 ",
          description2: "      .",
          image: "https://dl.poker.pmang.cloud/upload/poker_special1.png",
          detailImage: "https://dl.poker.pmang.cloud/upload/poker_special1_detail.png",
          isVisible: true,
          category: ""
        },
        {
          id: 2,
          productId: "PK002",
          name: " (30)",
          adminTag: "",
          displayName: "  30 ",
          price: 19900,
          startDate: "2024-03-01",
          endDate: "2024-03-31",
          description: "   30 ",
          description2: "      .",
          image: "https://dl.poker.pmang.cloud/upload/poker_special1.png",
          detailImage: "https://dl.poker.pmang.cloud/upload/poker_special1_detail.png",
          isVisible: true,
          category: ""
        }
      ]
    };
    return initialData[shop] || [];
  });

  const categories = {
    home: ["", "/", ""],
    newmajgo: ["", "", "", "", ""],
    poker: ["", "", "", ""]
  };

  const [editingProduct, setEditingProduct] = useState(null);
  const [showPreview, setShowPreview] = useState(false);
  const [previewProduct, setPreviewProduct] = useState(null);
  const [searchCriteria, setSearchCriteria] = useState('name');
  const [searchTerm, setSearchTerm] = useState('');
  const [visibilityFilter, setVisibilityFilter] = useState('all');

  //    state 
  const [sortConfig, setSortConfig] = useState({
    key: 'createdAt', //  
    direction: 'desc'  //  
  });

  const handleEditProduct = (product) => {
    setEditingProduct({ ...product });
  };

  const handleSaveEdit = () => {
    setProducts(products.map(product => 
      product.id === editingProduct.id ? editingProduct : product
    ));
    setEditingProduct(null);
  };

  const handleCancelEdit = () => {
    setEditingProduct(null);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setEditingProduct({
      ...editingProduct,
      [name]: value
    });
  };

  const toggleVisibility = (productId) => {
    setProducts(products.map(product => 
      product.id === productId 
        ? { ...product, isVisible: !product.isVisible }
        : product
    ));
  };

  const handlePreview = (product) => {
    setPreviewProduct(product);
    setShowPreview(true);
  };

  const closePreview = () => {
    setShowPreview(false);
    setPreviewProduct(null);
  };

  const handleSearch = () => {
    const filteredProducts = products.filter(product => {
      switch (searchCriteria) {
        case 'productId':
          return product.productId.includes(searchTerm);
        case 'name':
          return product.name.includes(searchTerm);
        case 'adminTag':
          return product.adminTag.includes(searchTerm);
        case 'displayName':
          return product.displayName.includes(searchTerm);
        case 'price':
          return product.price.toString().includes(searchTerm);
        case 'category':
          return product.category.includes(searchTerm);
        default:
          return true;
      }
    });
    setProducts(filteredProducts);
  };

  const getFilteredProducts = () => {
    let filtered = [...products];
    
    if (searchTerm) {
      filtered = filtered.filter(product => {
        switch (searchCriteria) {
          case 'productId':
            return product.productId.includes(searchTerm);
          case 'name':
            return product.name.includes(searchTerm);
          case 'adminTag':
            return product.adminTag.includes(searchTerm);
          case 'displayName':
            return product.displayName.includes(searchTerm);
          case 'price':
            return product.price.toString().includes(searchTerm);
          case 'category':
            return product.category.includes(searchTerm);
          default:
            return true;
        }
      });
    }
    
    switch (visibilityFilter) {
      case 'visible':
        return filtered.filter(product => product.isVisible);
      case 'hidden':
        return filtered.filter(product => !product.isVisible);
      default:
        return filtered;
    }
  };

  //     
  const handleDragEnd = (result) => {
    if (!result.destination) return;

    const items = Array.from(products);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    setProducts(items);
  };

  //  
  const sortProducts = (products) => {
    return [...products].sort((a, b) => {
      switch (sortConfig.key) {
        case 'price':
          return sortConfig.direction === 'asc' 
            ? a.price - b.price 
            : b.price - a.price;
        case 'name':
          return sortConfig.direction === 'asc'
            ? a.name.localeCompare(b.name)
            : b.name.localeCompare(a.name);
        case 'createdAt':
          return sortConfig.direction === 'asc'
            ? new Date(a.createdAt) - new Date(b.createdAt)
            : new Date(b.createdAt) - new Date(a.createdAt);
        default:
          return 0;
      }
    });
  };

  //    
  const handleSort = (key) => {
    setSortConfig(prevConfig => ({
      key,
      direction: 
        prevConfig.key === key 
          ? prevConfig.direction === 'asc' ? 'desc' : 'asc'
          : 'desc'
    }));
  };

  //       
  const getFilteredAndSortedProducts = () => {
    let filtered = getFilteredProducts();
    return sortProducts(filtered);
  };

  return (
    <div className="bg-white shadow rounded-lg p-6">
      <h2 className="text-2xl font-bold mb-6"> </h2>
      
      {/*     */}
      <div className="flex flex-col space-y-4 mb-6">
        {/*   */}
        <div className="flex items-center">
        <select
          value={searchCriteria}
          onChange={(e) => setSearchCriteria(e.target.value)}
          className="border rounded px-3 py-2 mr-2"
        >
          <option value="productId"></option>
          <option value="name"></option>
          <option value="adminTag">Admin Tag</option>
          <option value="displayName"></option>
          <option value="price"></option>
          <option value="category"></option>
        </select>
        <input
          type="text"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          placeholder=" "
          className="border rounded px-3 py-2 flex-1"
        />
        <button
          onClick={handleSearch}
          className="bg-blue-500 text-white px-4 py-2 rounded ml-2"
        >
          
        </button>
        </div>
        
        {/*     */}
        <div className="flex items-center justify-between">
          {/*    */}
          <div className="flex items-center">
            <label className="mr-2 text-gray-700"> :</label>
            <select
              value={visibilityFilter}
              onChange={(e) => setVisibilityFilter(e.target.value)}
              className="border rounded px-3 py-2 w-40"
            >
              <option value="all"></option>
              <option value="visible"> </option>
              <option value="hidden"> </option>
            </select>
            
            <span className="ml-4 text-sm text-gray-600">
               {getFilteredProducts().length} 
            </span>
          </div>

          {/*   */}
          <div className="flex items-center space-x-2">
            <span className="text-gray-700">:</span>
            <div className="flex bg-gray-100 rounded-lg p-1">
              <button
                onClick={() => handleSort('price')}
                className={`px-3 py-1 rounded-md text-sm transition-colors ${
                  sortConfig.key === 'price'
                    ? 'bg-white shadow text-blue-600'
                    : 'text-gray-600 hover:bg-gray-200'
                }`}
              >
                 {sortConfig.key === 'price' && (
                  <span>{sortConfig.direction === 'asc' ? '' : ''}</span>
                )}
              </button>
              <button
                onClick={() => handleSort('createdAt')}
                className={`px-3 py-1 rounded-md text-sm transition-colors ${
                  sortConfig.key === 'createdAt'
                    ? 'bg-white shadow text-blue-600'
                    : 'text-gray-600 hover:bg-gray-200'
                }`}
              >
                 {sortConfig.key === 'createdAt' && (
                  <span>{sortConfig.direction === 'asc' ? '' : ''}</span>
                )}
              </button>
              <button
                onClick={() => handleSort('name')}
                className={`px-3 py-1 rounded-md text-sm transition-colors ${
                  sortConfig.key === 'name'
                    ? 'bg-white shadow text-blue-600'
                    : 'text-gray-600 hover:bg-gray-200'
                }`}
              >
                 {sortConfig.key === 'name' && (
                  <span>{sortConfig.direction === 'asc' ? '' : ''}</span>
                )}
              </button>
            </div>
          </div>
        </div>
      </div>
      
      {/*    */}
      {showPreview && previewProduct && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-lg p-6 max-w-md w-full relative">
            <button className="absolute top-2 right-2 text-gray-500" onClick={closePreview}>
              
            </button>
            <div className="aspect-w-1 aspect-h-1 w-full mb-4">
              <img
                src={previewProduct.detailImage}
                alt={previewProduct.name}
                className="w-full h-[300px] object-contain rounded"
              />
            </div>
            <h2 className="text-xl font-bold mb-2">{previewProduct.name}</h2>
            <p className="text-sm text-gray-600 mb-4">{previewProduct.description}</p>
            <div className="flex justify-between items-center">
              <span className="text-lg font-bold">{previewProduct.price.toLocaleString()}</span>
              <button 
                className="px-4 py-2 bg-green-500 text-white rounded"
                onClick={() => {
                  setShowPreview(false);
                  setPreviewProduct(null);
                }}
              >
                
              </button>
            </div>
          </div>
        </div>
      )}
      
      {/*      */}
      <DragDropContext onDragEnd={handleDragEnd}>
        <Droppable droppableId="products">
          {(provided) => (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              className="space-y-4"
            >
              {getFilteredAndSortedProducts().map((product, index) => (
                <Draggable
                  key={product.id}
                  draggableId={product.id.toString()}
                  index={index}
                >
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      className={`border rounded-lg p-4 ${
                        snapshot.isDragging ? 'bg-gray-50' : 'bg-white'
                      }`}
                    >
                      {editingProduct && editingProduct.id === product.id ? (
              <div className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700">ID</label>
                    <input
                      type="text"
                      name="productId"
                      value={editingProduct.productId}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                      disabled
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700"></label>
                    <input
                      type="text"
                      name="name"
                      value={editingProduct.name}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                      disabled
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700">Admin TAG</label>
                    <input
                      type="text"
                      name="adminTag"
                      value={editingProduct.adminTag}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                      disabled
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700"></label>
                    <input
                      type="text"
                      name="displayName"
                      value={editingProduct.displayName}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                      disabled
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700"></label>
                    <input
                      type="number"
                      name="price"
                      value={editingProduct.price}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                      disabled
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700"></label>
                    <select
                      name="category"
                      value={editingProduct.category}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3"
                    >
                      {categories[shop].map((category) => (
                        <option key={category} value={category}>
                          {category}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700"></label>
                    <input
                      type="date"
                      name="startDate"
                      value={editingProduct.startDate}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                      disabled
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700"></label>
                    <input
                      type="date"
                      name="endDate"
                      value={editingProduct.endDate}
                      onChange={handleInputChange}
                      className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                      disabled
                    />
                  </div>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"> </label>
                  <textarea
                    name="description"
                    value={editingProduct.description}
                    onChange={handleInputChange}
                    className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                    rows="3"
                    disabled
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"> 2</label>
                  <textarea
                    name="description2"
                    value={editingProduct.description2}
                    onChange={handleInputChange}
                    className="mt-1 block w-full border rounded-md shadow-sm py-2 px-3 bg-gray-100"
                    rows="3"
                    disabled
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"></label>
                  <div className="mt-1 flex items-center space-x-4">
                    <div className="flex-1">
                      <input
                        type="file"
                        name="image"
                        accept="image/*"
                        onChange={(e) => {
                          const file = e.target.files[0];
                          if (file) {
                            const reader = new FileReader();
                            reader.onloadend = () => {
                              setEditingProduct({
                                ...editingProduct,
                                image: reader.result
                              });
                            };
                            reader.readAsDataURL(file);
                          }
                        }}
                        className="block w-full text-sm text-gray-500
                          file:mr-4 file:py-2 file:px-4
                          file:rounded-md file:border-0
                          file:text-sm file:font-semibold
                          file:bg-blue-50 file:text-blue-700
                          hover:file:bg-blue-100"
                      />
                    </div>
                    {editingProduct.image && (
                      <div className="w-24 h-24 border rounded-md overflow-hidden">
                        <img
                          src={editingProduct.image}
                          alt="  "
                          className="w-full h-full object-cover"
                        />
                      </div>
                    )}
                  </div>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700"> </label>
                  <div className="mt-1 flex items-center space-x-4">
                    <div className="flex-1">
                      <input
                        type="file"
                        name="detailImage"
                        accept="image/*"
                        onChange={(e) => {
                          const file = e.target.files[0];
                          if (file) {
                            const reader = new FileReader();
                            reader.onloadend = () => {
                              setEditingProduct({
                                ...editingProduct,
                                detailImage: reader.result
                              });
                            };
                            reader.readAsDataURL(file);
                          }
                        }}
                        className="block w-full text-sm text-gray-500
                          file:mr-4 file:py-2 file:px-4
                          file:rounded-md file:border-0
                          file:text-sm file:font-semibold
                          file:bg-blue-50 file:text-blue-700
                          hover:file:bg-blue-100"
                      />
                    </div>
                    {editingProduct.detailImage && (
                      <div className="w-24 h-24 border rounded-md overflow-hidden">
                        <img
                          src={editingProduct.detailImage}
                          alt="  "
                          className="w-full h-full object-cover"
                        />
                      </div>
                    )}
                  </div>
                </div>
                <div className="flex justify-end space-x-2">
                  <button
                    onClick={handleCancelEdit}
                    className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
                  >
                    
                  </button>
                  <button
                    onClick={handleSaveEdit}
                    className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600"
                  >
                    
                  </button>
                </div>
              </div>
            ) : (
              <div className="flex items-start space-x-4">
                          <div
                            {...provided.dragHandleProps}
                            className="flex items-center px-2 cursor-move"
                          >
                            <svg
                              className="w-6 h-6 text-gray-400"
                              fill="none"
                              stroke="currentColor"
                              viewBox="0 0 24 24"
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M4 8h16M4 16h16"
                              />
                            </svg>
                          </div>
                          <img
                            src={product.image}
                            alt={product.name}
                            className="w-24 h-24 object-cover rounded"
                          />
                <div className="flex-1">
                  <div className="flex items-center space-x-2">
                              <h3 className="text-lg font-medium">
                                {product.name}
                              </h3>
                              <span className="text-sm text-gray-500">
                                ({product.productId})
                              </span>
                  </div>
                  <div className="mt-1">
                              <span className="text-sm text-blue-500">
                                Admin TAG: {product.adminTag}
                              </span>
                              <span className="ml-4 text-sm text-gray-500">
                                : {product.displayName}
                              </span>
                  </div>
                            <p className="text-gray-600 mt-2">
                              {product.description}
                            </p>
                  <div className="mt-2">
                              <span className="text-gray-500">
                                : {product.category}
                              </span>
                              <span className="ml-4 text-gray-500">
                                : {product.price.toLocaleString()}
                              </span>
                  </div>
                  <div className="mt-1 text-sm text-gray-500">
                    : {product.startDate} ~ {product.endDate}
                  </div>
                </div>
                <div className="flex items-center space-x-2">
                  <button
                    onClick={() => handlePreview(product)}
                    className="p-2 text-gray-500 hover:text-gray-700"
                    title=""
                  >
                    <HiEye className="w-5 h-5" />
                  </button>
                  <button
                    onClick={() => toggleVisibility(product.id)}
                    className={`px-3 py-1 rounded-md text-sm ${
                      product.isVisible 
                        ? 'bg-green-500 text-white hover:bg-green-600' 
                        : 'bg-red-500 text-white hover:bg-red-600'
                    }`}
                  >
                    {product.isVisible ? '' : ''}
                  </button>
                  <button
                    onClick={() => handleEditProduct(product)}
                    className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600"
                  >
                    
                  </button>
                </div>
              </div>
            )}
          </div>
                  )}
                </Draggable>
        ))}
              {provided.placeholder}
      </div>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
};

export default ProductManagement; 